<template>
  <div class="dashboard-container">
    <header class="dashboard-header">
      <h1>Dashboard Administrativo</h1>
      <p>Bem-vindo ao painel do administrador!</p>
    </header>

    <main class="dashboard-content">
      <section class="card card-users">
        <h2>ðŸ‘¥ UsuÃ¡rios</h2>
        <p>Gerencie contas e permissÃµes.</p>
        <div v-if="adminData && adminData.userCount">
          Total de UsuÃ¡rios: {{ adminData.userCount }}
        </div>
        <div v-else>
          Carregando nÃºmero de usuÃ¡rios...
        </div>
      </section>

      <section class="card card-reports">
        <h2>ðŸ“Š RelatÃ³rios</h2>
        <p>Acompanhe dados importantes.</p>
        <div v-if="adminData && adminData.reportCount">
          NÃºmero de RelatÃ³rios: {{ adminData.reportCount }}
        </div>
        <div v-else>
          Carregando nÃºmero de relatÃ³rios...
        </div>
      </section>

      <section class="card card-settings">
        <h2>âš™ï¸ ConfiguraÃ§Ãµes</h2>
        <p>Personalize o sistema.</p>
        <div v-if="adminData && adminData.settingCount">
          NÃºmero de ConfiguraÃ§Ãµes: {{ adminData.settingCount }}
        </div>
        <div v-else>
          Carregando nÃºmero de configuraÃ§Ãµes...
        </div>
      </section>
    </main>

    <div v-if="error" class="error">{{ error }}</div>
    <button @click="logout">Sair</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      adminData: null,
      error: null,
      ngrokURL: 'https://localhost:3000/',
    };
  },
  mounted() {
    this.fetchAdminDashboardData();
  },
  methods: {
    async fetchAdminDashboardData() {
      const token = localStorage.getItem('adminToken');

      if (!token) {
        this.$router.push('./login');
        return;
      }

      try {
        const response = await fetch(`${this.ngrokURL}/api/admin/dashboard`, {
  method: 'GET',
  headers: {
    'Authorization': `Bearer ${token}`,
    'Content-Type': 'application/json',
  },
});

        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(`Erro na requisiÃ§Ã£o: ${response.status} - ${errorData.message || response.statusText}`);
        }

        this.adminData = await response.json();
        this.error = null;
      } catch (error) {
        console.error('Erro ao buscar dados do painel:', error);
        this.error = 'Erro ao carregar os dados do painel. Por favor, faÃ§a login novamente.';
        localStorage.removeItem('adminToken');
        this.$router.push('/login');
      }
    },
    logout() {
      localStorage.removeItem('adminToken');
      this.$router.push('/login');
    },
  },
};
</script>
<style scoped>
h1 {
  margin-bottom: 20px;
}

.admin-info {
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.error {
  color: red;
  margin-top: 10px;
}

button {
  padding: 10px 15px;
  background-color: #f44336;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1em;
}

button:hover {
  background-color: #d32f2f;
}
</style>